.work {
    background-image: url("/img/background.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    padding-bottom: 63px;
    
}

.work__wrapper {
    background: #232323;
    border-radius: 0 0 27px 27px;
    padding: 145px 107px 108px;
    margin: 0 -15px;
    width: 1114px;
    color: #f0f0f0;
}

.work__title {
    text-transform: uppercase;
    font-weight: 500;
    font-size: 32px;
    width: 776px;
    text-align: center;
}

.work__documents {
    margin-top: 55px;

    &-item {
        width: 286px;
        text-align: center;
        font-weight: 500;
        font-size: 24px;
        text-transform: uppercase;

    }
}