.header {
    background-image: url("/img/background_header.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    padding-top: 261px;
}

.header__content {
    margin: 0 auto;
    display: block;
    justify-content: center;
    text-align: center;
    
}

.header__content-title,
.header__content-one,
.header__content-two {
    text-transform: uppercase;
    font-size: 128px;
    font-weight: 700;
    color: rgba(189, 57, 51, 1);
}

.header__content-one {
    font-size: 64px;
    color: #232323;
}

.header__content-two {
    color: #232323;
    font-weight: 500;
    font-size: 24px;
}

.logo {
    display: block;
    margin: 0 auto;
}

.list-statistics {
    display: flex;
    justify-content: center;
    margin-bottom: 37px;

}

.list-statistics__item {
    width: 113px;
    margin-left: 30px;
}

.list-statistics__number {
    font-weight: 700;
    font-size: 32px;
}

.list-statistics__text {
    font-weight: 500;
    font-size: 14px;
    
}

.link__buy {
    text-decoration: none;
    margin-top: 37px;
    padding: 20px 96px;
    background: radial-gradient(45.16% 45.16% at 50% 11.29%, rgba(255, 255, 255, 0.15) 0%, rgba(255, 255, 255, 0) 100%),
    linear-gradient(180deg, #AB2022 0%, #C13C36 100%);
    box-shadow: 0px 2px 0px #C73838,
    inset 0px -3px 36px rgba(255, 255, 255, 0.15);
    border-radius: 8px;
    color: #F0F0F0;
    font-weight: 500;
    font-size: 24px;
    text-transform: uppercase;
}

.link__down {
    display: block;
    margin-top: 37px;
    position: relative;
    top: 27px;
}

.image-block {
    display: block;
    margin: 0 -15px;
}